body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #edf5ff;
  background: -webkit-linear-gradient(left, #edf5ff 0%,#ffffff 25%,#ffffff 50%,#ffffff 75%,#edf5ff 100%);
  background: -webkit-gradient(linear, left top, right top, from(#edf5ff),color-stop(25%, #ffffff),color-stop(50%, #ffffff),color-stop(75%, #ffffff),to(#edf5ff));
  background: -o-linear-gradient(left, #edf5ff 0%,#ffffff 25%,#ffffff 50%,#ffffff 75%,#edf5ff 100%);
  background: linear-gradient(to right, #edf5ff 0%,#ffffff 25%,#ffffff 50%,#ffffff 75%,#edf5ff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#edf5ff', endColorstr='#edf5ff',GradientType=1 );
}

.container {
  padding: 5vh 5vw;
}

.table tr:hover {
  background: #fffaf3!important;
  color: #573a08!important;
}

.field .radio {
  margin-right: 20px;
}

input[type='number'] {
  -moz-appearance:textfield;
}
/* Webkit browsers like Safari and Chrome */
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.loginChangeLink {
  color: #777;
  font-size: 12px;
  float: right;
  line-height: 36px;
}

.ui.label {
  font-weight: normal;
}
.segment-loader {
  border: none !important;
  border-radius: 0 !important;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  background: none !important;
}

pre{
  white-space: pre-wrap
}
.input-fake{
  border: none;
  background: transparent;
}


